.mainImg {
    position: relative;
    z-index: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    img {
        width: 100%;
        object-fit: cover;
    }
    .imgText {
        text-align: center;
        position: absolute;
        z-index: 1;
        font-size: 85px;
        color: white;
        max-width: 70%;
        outline: 0px solid red;
        align-self: center;
        //opacity: 0;
        transform: translate(0px, 120%);
        // transition: 2s;
    }  
    @media screen and (max-width: 1024px){
        .imgText {
            font-size: 50px;
        }
    }
    @media screen and (max-width: 414px) {
            img {
                height: 500px;
            }
        .imgText {
            font-size: 40px;
        }
    }
}
